// Orquestra Form Wrapper
#BoxFrmExecute {
  font-family: "Roboto", "Helvetica Neue", Helvetica, Arial, sans-serif;

  *, *:after, *:before {
    box-sizing: border-box;
  }
}

// Input Radio: positioning fix
.radio {
  display: flex;
  align-items: center;

  input[type=radio] {
    margin-right: 8px;
  }
}
